body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}


header {
  font-size: 30px;
  color: #6BA2A2;
}

input[type="number"] {
  text-align: center;
  width: 50px;
  margin: 10px;
  padding: 10px;
  border-radius: 10px;
}

input[type="submit"] {
  padding: 10px;
  margin: 10px;
  font-size: 30px;
  border: none;
  background-color: #6BA2A2;
  color: white;
  border-radius: 10px;
}

.error {
  color: red;
}

.result {
  color: #3CBC8D
}
